* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
li {
  padding: 0;
  margin: 0;
}

.country__name {
  font-weight: 700;
  font-size: 50px;
}
.country__info {
  font-size: 30px;
}
.list {
  font-size: 25px;
}
.descr {
  font-weight: 600;
}
.title {
  display: flex;
  align-items: center;
  gap: 5px;
}
